import BrowserHandler from '../common/browserHandler';
import { isIFrame, log } from './content.helper';

('use strict');

log(`navigated to: ${location.href}`);

function notifyAuthentication(type: string, params: any): void {
    const message = {
        activity: 'authorization',
        origin: window.location.origin,
        type,
        close: true,
    };

    Object.assign(message, params);

    // clear the hash so refresh will not cause another notification
    location.hash = '';
    BrowserHandler.runtime.sendMessage(message);
}

function isResponse(): boolean {
    return location.hash.length > 0;
}

function parseParams(href: string): any {
    const baseAndParams = href.split(/[?#]/);
    if (baseAndParams.length < 2) {
        return {};
    }
    let i = 1;
    var result: any = {};
    while (i < baseAndParams.length) {
        href = baseAndParams[i];
        var params = new URLSearchParams(href);
        for (var [name, value] of params.entries()) {
            result[name] = value;
        }
        i++;
    }
    return result;
}

// Function is triggered when the live login window pops up
window.addEventListener('load', function (): void {
    const params = parseParams(location.href);

    if (!(params.state && params.state.includes('OfficeOnlineExtension'))) {
        return;
    }

    if (window.location.origin === 'https://login.microsoftonline.com') {
        if (params.code) {
            notifyAuthentication('o365UserService', params);
        } else if (isResponse() && location.pathname === '/common/oauth2/nativeclient') {
            notifyAuthentication('msidUserService', params);
        }
    } else if (window.location.origin === 'https://login.live.com') {
        if (params.code) {
            notifyAuthentication('msaUserService', params);
        } else if (location.pathname === '/oauth20_authorize') {
            notifyAuthentication('msidUserService', params);
        }
    }
});
